import { Component,NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { AnnouncementModal } from '../../modals/announcement/announcement';
import { ChannelProvider } from '../../providers/channel/channel';

import * as moment from 'moment';
/**
 * Generated class for the EventsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-events',
  templateUrl: 'events.html',
})
export class EventsPage {
    
    
    public loading:boolean;
    public subscribedChannel:any;   
    public announcements: Array<any>; 

    constructor(public navCtrl: NavController, public navParams: NavParams,public storage: Storage, public modalCtrl: ModalController, public events : Events, public channelProvider: ChannelProvider, private ngZone: NgZone) {
        this.announcements = [];
        
        this.events.subscribe("mqtt:message", (message) => {
            this.getAnnouncements();            
        });        
    }

  
    ionViewWillEnter(){
        this.loading = true;
        this.subscribedChannel = null;
        this.storage.get("subscribedChannel").then((data) => {
            console.log(data);
            this.loading = false;
            if (data){
                this.subscribedChannel = data;
                this.getAnnouncements();
            }
        });       
    }

    
    public getAnnouncements(){
        if (this.subscribedChannel){
            this.channelProvider.getAnnouncements(this.subscribedChannel.hubId).then((announcements:Array<any>) => {
                this.ngZone.run(() => {
                    this.announcements = this.filterAnnouncements(announcements);
                });
                this.storage.set("announcements", announcements);
                console.log(announcements);
            }).catch(() => {
                this.storage.get("announcements").then((data) => {
                    if (data){
                        this.ngZone.run(() => {
                            this.announcements = this.filterAnnouncements(data);
                        });
                    }
                });                 
            });    
        }    
    }  
    
    private filterAnnouncements(announcements){
        let filteredAnnouncements = [];

        for (let announcement of announcements){
            let announcementDateEnd = moment(announcement.eventEnd, 'DD-MM-YYYY hh:mm A');
            let announcementDateStart = moment(announcement.eventStart, 'DD-MM-YYYY hh:mm A');
            let today = moment();

            if (announcementDateEnd.isAfter(today) /* && announcementDateStart.isBefore(today)*/){
                filteredAnnouncements.push(announcement);
            }
        }
        
        return filteredAnnouncements;        
    }      
    
    public openAnnouncement(announcement){
        let modal = this.modalCtrl.create(AnnouncementModal, {announcement:announcement}); 
        modal.present();          
    }
    
    public stripHtml(html:string){
        return html.replace(/<(?:.|\n)*?>/gm, '');
    }

}



