import { Component } from '@angular/core';
import { Platform, App, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage } from '../pages/tabs/tabs';
import { LoginPage } from '../pages/login/login';
import { SignUpPage } from '../pages/sign-up/sign-up';

import { Storage } from '@ionic/storage';

import { AuthenticationProvider } from '../providers/authentication/authentication';

import {AppSettings} from './app.settings';

import { LocalNotifications } from '@ionic-native/local-notifications';

import { BackgroundMode } from '@ionic-native/background-mode';


declare var mqtt: any;
import * as mqttJs from 'mqtt';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = LoginPage;
  public useNative:boolean = false;
  public client:any;

  constructor(public app: App, public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, public authProvider: AuthenticationProvider, public storage: Storage, public events: Events, private backgroundMode: BackgroundMode, private localNotifications: LocalNotifications) {
    platform.ready().then(() => {
        // Okay, so the platform is ready and our plugins are available.
        // Here you can do any higher level native things you might need.
        //statusBar.styleDefault();
        if (this.platform.is("android")) {
            this.statusBar.backgroundColorByHexString("#952517");
            this.statusBar.styleLightContent();
        }
        else{
            this.statusBar.styleLightContent();
        }
        
        
        this.storage.get("user").then((token) => {
            console.log(token);
            if (token){
                this.app.getRootNav().setRoot(TabsPage);
                this.app.getRootNav().popToRoot();                       

            }
            setTimeout(() => {this.splashScreen.hide()},500);
        }).catch(() => {
            this.splashScreen.hide();
        });
        
        if (platform.is("cordova") && (platform.is("android") || platform.is("ios"))){
            this.useNative = true;
        }  
        
        
        if (this.useNative){
            console.log(mqtt);
            console.log("connecting mqtt");
            //use the native MQTT plugin
            mqtt.init({
                host:"b-f4be5e8b-748c-4ac6-875f-56badddbf4c7-1.mq.ap-southeast-2.amazonaws.com",
                port:8883,
                username:"wentity",
                password:'wentity@1234',
                ssl:true,
                offlineCaching:false
            });   
            
            mqtt.connect();
            
            mqtt.on("connect", () => {
                console.log("connect success");

                this.storage.get("subscription").then((data) => {
                    if (data){
                        this.mqttSubscribe(data.hubId);
                    }
                });                 
            }, (status) => {
                //alert("failed: " + status);
                console.log(status);
            })          
        }   
        
        else{
            /*
             * for non android and ios
            this.client  = mqttJs.connect({
                username:"wentity",
                password:"wentity@1234",
                host:'b-f4be5e8b-748c-4ac6-875f-56badddbf4c7-1.mq.ap-southeast-2.amazonaws.com',
                port:8883,
                protocol: 'wss'
            });
            this.client.on('connect', () => {
                console.log("success");
                this.storage.get("subscription").then((data) => {
                    if (data){
                        this.mqttSubscribe(data.hubId);
                    }
                });                  
            });
            */
           
        } 
        
        


        this.events.subscribe('channel:subscribed', (hubId) => {
            console.log(hubId);
            this.mqttSubscribe(hubId);
        });            
        this.events.subscribe('channel:unsubscribed', (hubId) => {
            console.log(hubId);
            this.mqttUnsubscribe(hubId);
        });         

        this.backgroundMode.setDefaults({silent:true});
        this.backgroundMode.enable();
      
      
    });
  }
  
  
    public mqttSubscribe(hubId){
        console.log(hubId);
        if (this.useNative){
            console.log("subscribing");
            mqtt.subscribe({
                topic:hubId,
                qos:2
            }); 
            console.log("running callback");
            mqtt.on("subscribe", (subscription) => {
                console.log("here");
                console.log(subscription);
                this.mqttListen(hubId);
            }, (e) => {
                console.log(e);
            });
        }
        else if (this.client){
            this.client.subscribe(hubId, {qos: 2});
            this.mqttListen(hubId);
        }      
    }
    
    public mqttListen(hubId){
        if (this.useNative){
           mqtt.on("message", (message) => {
                console.log(message);
                
                this.events.publish("mqtt:message", {message:message});
                
                alert("An update to your Salat times or an announcement has been made.");    
                
                this.localNotifications.schedule({
                  id: 1,
                  title: 'Update Received',
                  text: 'An update to your Salat times or an announcement has been made.'
                });                            
           }) 
        }
        else if (this.client){
            this.client.on('message', (value) => {
                console.log(value);
                this.events.publish("mqtt:message", {message:value});
                
                alert("An update to your Salat times or an announcement has been made.");    

                                            
            });
        }     
    }
    
    public mqttUnsubscribe(hubId){
        if (this.useNative){
            mqtt.unsubscribe({
                topic:hubId
            }); 
            mqtt.on("unsubscribe", (topic) => {
                console.log(topic);
            }, (e) => {
                console.log(e);
            });    
        } 
        else if (this.client){
            this.client.unsubscribe(hubId, {});
        } 
    }    
    
}



